@import "whiteglass";


.post-content p {
	margin-bottom: 0;
	/*text-indent: 1.25em;*/
	text-align: justify;
	font-size: 18px;
  line-height: 1.25em;
  hyphens: auto;
}


.post-content hr {
	border: none;
	margin: 3em auto;
  background: none;


  text-align: center;
  border:none;
  border-top: 1px dotted #8c8b8b;
  max-width: 6em;
  overflow: visible;

  &:after {
    content: ' ยง ยง ยง';
    display: inline-block;
    position: relative;
    top: -14px;
    padding: 0 10px;
    background: #fdfdfd;
    color: #8c8b8b;
    font-size: 18px;	
  }
}

/*.post-content p:first-of-type,
hr>p,
blockquote>p
 {
	text-indent:0
}*/

.post-content p+p {
  text-indent: 1.25em;
}


blockquote {
	margin-bottom: 3em;
};


.share-buttons {
	margin-top: 3em;
	opacity: 0.6;
}
.share-buttons:hover{
	opacity: 1;
        transition: opacity 0.25s ease-in-out;
}


a {
 .social-media-list &:hover {
    text-decoration: none;

    .username {
      text-decoration: underline;
    }
  }

}
.social {

  margin-bottom:5em;

  ul {
    display:inline;
    list-style-type: none;
    margin:0;
  }

  li {
    display:inline;
  }
}

.svg-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  fill: currentColor;
  padding: 5px 3px 2px 5px;
  vertical-align: text-bottom;
}

.footer-cr {
  font-size: x-small;
}

.post-post {
  opacity: 0.8;
  text-align: center;
  font-style: italic;
  margin: 3em;
}

.copy {
  border: solid 1px #8c8b8b;
  padding: 0.5em;
  font-size: small !important;
  font-style: italic;
  margin-top: 3em;
  display: block;
  color: #8c8b8b;
  text-align: justify;
  text-indent: none;
  border-radius: 5px;
}

.post-social {
  margin: 3em auto;
}